<template>
  <div class="login-wrap">
    <div class="login_plate_top"></div>
    <div class="login_plate_bottom"></div>

    <!--登录面板-->
    <div class="loginBoard">
      <p class="ms-title"></p>
      <div class="ms-login">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px" class="demo-ruleForm" @keyup.native.enter="submitForm('ruleForm')">

          <p class="login_title">账号/手机号码</p>
          <el-form-item prop="username" class="textInput">
            <el-input v-model="ruleForm.username" placeholder="请输入账号/手机号码" @keyup.native="noSpace('username')" @blur="noSpace('username')"></el-input>
          </el-form-item>

          <p class="login_title">密码</p>
          <el-form-item prop="password" class="textInput">
            <el-input type="password" placeholder="请输入密码" v-model="ruleForm.password" @keyup.native="noSpace('password')" @blur="noSpace('password')" @keyup.enter.native="submitForm('ruleForm')"></el-input>
          </el-form-item>
          <p class="textForgetPassword" @click="retrieveThePassword">忘记密码？</p>

          <p class="login_title">验证码</p>
          <el-form-item prop="verificationCode" class="verificationCode">
            <el-input v-model="ruleForm.verificationCode" placeholder="请输入验证码" ></el-input>
            <div class="verificationCodeImage" @click="refreshVerificationCode">
              <img :src="this.verificationCodeUrl">
            </div>
            <span class="refreshVerificationCode" @click="refreshVerificationCode">换一换</span>
          </el-form-item>

          <div class="login-btn">
            <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
          </div>
        </el-form>
      </div>
    </div>

    <!-- 阴影遮罩 -->
    <div class="shadow_mask" v-if="shadowMask"></div>

    <!-- 首次登录设置  个人信息 弹出框 -->
    <div class="personal_information" v-if="information">
      <i class="close_icon" @click="deleteInformation"></i>
      <p class="title">请设置您的个人信息</p>
      <el-form :model="form" ref="form" :rules="personalRules" label-position="right" label-width="100px" class="demo-ruleForm">
        <el-form-item label="真实姓名" prop="realName" required>
          <el-input v-model="form.realName" placeholder="请输入真实姓名"></el-input>
        </el-form-item>
        <el-form-item label="身份证号码" prop="idCard" required>
          <el-input v-model="form.idCard" placeholder="请输入身份证号码"></el-input>
        </el-form-item>
        <el-form-item label="警官证号码" prop="policeIdCard">
          <el-input v-model="form.policeIdCard" placeholder="请输入警官证号码（选填）"></el-input>
        </el-form-item>
        <el-form-item label="邮箱地址" prop="email">
          <el-input v-model="form.email" placeholder="请输入邮箱地址（选填）"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="mobilePhone" required>
          <el-input v-model="form.mobilePhone" placeholder="请输入手机号码"></el-input>
        </el-form-item>
        <el-form-item label="短信验证码" prop="verificationCode" required>
          <el-input v-model="form.verificationCode" placeholder="短信验证码">
            <el-button id="timeLimitVerificationCode" type="primary" slot="append" @click="timeLimitVerificationCode">发送短信验证码</el-button>
          </el-input>
        </el-form-item>
        <el-form-item label="设置新密码" prop="newPassword" required>
          <el-input v-model="form.newPassword" placeholder="只能输入6~20位字母、数字、下划线"></el-input>
        </el-form-item>
        <el-form-item label="重复新密码" prop="repeatNewPassword" required>
          <el-input v-model="form.repeatNewPassword" placeholder="重复新密码"></el-input>
        </el-form-item>
        <el-form-item label-width="0" class="set_confirm_button">
          <el-button @click="setForm()">确定设置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 首次登录  设置安全问题 -->
    <div class="setting_safety_problem" v-if="settingSafetyProblem">
      <i class="close_icon" @click="deleteSettingSafetyProblem"></i>
      <p class="t_title">设置安全问题和答案</p>
      <div>
        <p>
          安全问题:
          <el-select v-model="safetyValue" placeholder="请选择安全问题">
            <el-option
              v-for="item in safetyOptions"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </p>
        <p>问题答案: <el-input v-model="safetyValueAnswer" placeholder="请输入问题答案"></el-input></p>
      </div>
      <el-button @click="sendUsersSettingSafetyProblem">确定</el-button>
    </div>

    <!-- 回答安全问题 -->
    <div class="safety_problem" v-if="safetyProblem" >
      <i class="close_icon" @click="deleteSafetyProblem"></i>
      <p class="title">请回答您设置的安全问题答案</p>
      <table cellspacing="20px">
        <tr>
          <td noWrap>安全问题：</td>
          <td align="left">{{answer}}</td>
        </tr>
        <tr>
          <td noWrap> 问题答案：</td>
          <td align="left"><el-input  @keyup.native.enter="jumpSearchPage" autoFocus placeholder="请输入答案" v-model="safetyProblemAnswer"></el-input></td>
        </tr>
      </table>
      <div class="set_confirm_button">
        <el-button type="primary" @click="jumpSearchPage">确定答案</el-button>
      </div>
    </div>

  </div>
</template>

<script>
/* eslint-disable */

import { publicEncrypt, privateDecrypt } from 'crypto-browserify'

export default {
  name: 'login',
  data: function () {
    return {
      answer: '',
      safetyProblemAnswer: '',
      shadowMask: false,
      safetyProblem: false,
      information: false,
      settingSafetyProblem: false,
      getLocalUser: {},
      verificationCodeUrl: '',
      ruleForm: {
        username: '',
        password: '',
        verificationCode: ''
      },
      uuid: '',
      rules: {
        username: [
          { required: true, message: '请输入账号/手机号码', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请输入正确的手机号码', trigger: 'blur'}
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 12, message: '请输入6-12位数字或字母的组合 ', trigger: 'blur' },
          { pattern: /^[0-9a-zA-Z]*$/g, message: '请输入6-12位数字或字母的组合', trigger: 'blur' }
        ],
        verificationCode: [
          { required: true, message: '请输入验证码', trigger: 'blur' }
        ]
      },
      form: {
        realName: '',
        idCard: '',
        policeIdCard: '',
        email: '',
        mobilePhone: '',
        verificationCode: '',
        newPassword: '',
        repeatNewPassword: '',
        genre: '',
        lastLoginTime: ''
      },
      personalRules: {
        realName: [
          { required: true, message: '请输入真实姓名', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请输入', trigger: 'blur' }
        ],
        idCard: [
          { required: true, message: '请输入身份证号码', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请输入正确的身份证号码', trigger: 'blur' }
        ],
        policeIdCard: [
          { required: false, message: '请输入警官证号码', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '', trigger: 'blur' }
        ],
        email: [
          { required: false, message: '请输入邮箱', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请输入', trigger: 'blur' }
        ],
        mobilePhone: [
          { required: true, message: '请输入手机号码', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请输入正确的手机号码', trigger: 'blur' }
        ],
        verificationCode: [
          { required: true, message: '请输入验证码', trigger: 'blur' }
          // { pattern: /^1[34578]\d{9}$/, message: '请输入正确的验证码', trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '请输入新密码', trigger: 'blur' },
          { min: 6, max: 20, message: '请输入6-20位数字或字母的组合 ', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请输入', trigger: 'blur' }
        ],
        repeatNewPassword: [
          { required: true, message: '请再次输入新密码', trigger: 'blur' },
          { min: 6, max: 20, message: '请输入6-20位数字或字母的组合 ', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请输入', trigger: 'blur' }
        ]
      },
      safetyOptions: [],
      safetyValue: '',
      safetyValueAnswer: ''
    }
  },
  mounted () {
    this.refreshVerificationCode()

    // 判断本地是否有一记住密码的用户,并将用户名，密码 自动赋值
    if (localStorage.getItem('ms_username')) {
      this.getLocalUser = JSON.parse(localStorage.getItem('ms_username'));
      let localUserLen = Object.keys(this.getLocalUser).length;
      this.ruleForm.username = this.getLocalUser[localUserLen-1].username;
      this.ruleForm.password = this.getLocalUser[localUserLen-1].password;
    }
  },
  methods: {

    // 关闭 —— 首次登录 个人信息设置页面
    deleteInformation: function () {
      this.information = false
      this.shadowMask = false
    },

    // 关闭 —— 用户登录回答安全问题页面
    deleteSafetyProblem: function () {
      this.safetyProblem = false
      this.shadowMask = false
    },

    // 关闭 —— 首次登录 设置安全问题页面
    deleteSettingSafetyProblem: function () {
      this.settingSafetyProblem = false
      this.shadowMask = false
    },

    // 请求验证码
    refreshVerificationCode: function () {
      let para = { responseType: 'arraybuffer' }
      gapi.getVerificationCode(para)
        .then((res) => {
          if (res.status === 200) {
            let blob = new Blob([res.data], {type: "application/vnd.ms-excel"});
            this.verificationCodeUrl = URL.createObjectURL(blob);
            this.uuid = res.headers.data
          } else {
            alert('请刷新验证码')
            return false
          }
        })
        .catch((error) => {
          console.log('网络故障 请重试')
          return false
        })
    },

    /* 找回密码 */
    retrieveThePassword: function () {

    },

    // 登录
    submitForm: function(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let public_key = `-----BEGIN PUBLIC KEY-----
MIIBIjANBgkqhkiG9w0BAQEFAAOCAQ8AMIIBCgKCAQEAwvyJIGJ7wd81UYorGtkv
YHgxFWUsvOIo2sE/tOHRaFbsyxrMxGCcUGqEirMUAlRBeskw/QlpWlfISe1mn214
RRQ9QmBtY4IPZEw//A55+4sXME4h2HgK0WtdLCTsLi3stQxUF8qctbvvtdhZfspF
8dZmFwW4zXlhgUtvjr+4eHH4hp6cJHYmbJ3XspWm60rWIWZM/y38zPg1KV3mn1Ai
wkwIXKAeCQi61Flftvs0XOWbCUDZdqCaqX4KyZZByqjoRfDIYPOlXUR/urfuj9zM
6JsJC+ns+xNXnPpNGjFuV3o42KVK7h4IXxv5j+KhGXQyAbkZ9k7ZPfUzZsHAvVqO
WQIDAQAB
-----END PUBLIC KEY-----`
          let es = publicEncrypt({
            padding: 1,
            key: public_key
          }, new Buffer(this.ruleForm.password), false)
          let para = {
            account : this.ruleForm.username,
            // password : es.toString('hex'),
            password: this.ruleForm.password,
            verificationCode: this.ruleForm.verificationCode,
            uuid : this.uuid
          }

          para = this.$qs.stringify(para)
          gapi.userLogin(para)
          .then((res) => {
            console.log('@用户信息@', res)
            if (res.code === 1) {
              localStorage.setItem('isLogin', this.ruleForm.username)
              localStorage.setItem('access_Token', res.data.accessToken)
              localStorage.setItem('userName', res.data.realName)
              localStorage.setItem('lastLoginTime', res.data.lastLoginTime)
              localStorage.setItem('genre', res.data.genre)

              localStorage.setItem('lastestAccount', this.ruleForm.username)
              localStorage.setItem('access_Token', res.data.accessToken)

              this.$store.dispatch('me/initMe', res.data)

              // 是首次登录用户弹出 个人信息填写页面
              if ( res.data.firstLoginFlag === true ) {
                this.information = true
                this.shadowMask = true
              } else {
                // 不是首次登录用户弹出 安全问题回答框
                this.shadowMask = true
                this.requestUserSecurityIssues()
                this.safetyProblem = true
              }
            } else {
              alert(res.failure)
              this.refreshVerificationCode()
              return false
            }
          })
          .catch((error) => {
            return false
          })
        } else {
          return false
        }
      })
    },

    // 首次登录设置个人信息
    setForm: function () {
      let pram = {
        realName: this.form.realName,
        idCard: this.form.idCard,
        policeIdCard: this.form.policeIdCard,
        email: this.form.email,
        mobilePhone: this.form.mobilePhone,
        verificationCode: this.form.verificationCode,
        newPassword: this.form.newPassword
      }
      console.log('@@@@@@@@@@@@@', pram)
      pram = this.$qs.stringify(pram)
      gapi.addPersonalInformation(pram)
      .then((res) => {
        console.log('##发送首次登录设置个人信息##', res)
        if (res.code === 1) {
          this.information = false
          this.settingSafetyProblem = true
          this.getAllSecurityQuestions()
        } else {
          console.log("##发送失败##", res.code)
          return false
        }
      })
      .catch((error) => {
        return false
      })
    },

    // 首次登录设置个人信息 —— 限时验证码
    timeLimitVerificationCode: function () {
      let pram = {
        mobilePhone: this.form.mobilePhone
      }
      console.log('&&phone&&', pram)
      gapi.sendUserSms(pram)
        .then((res) => {
          console.log(res)
          if (res.data.code === 1) {
            let num = 60
            let limitButton = document.getElementById('timeLimitVerificationCode')
            let timer = setInterval(function () {
              num--
              limitButton.innerHTML = num + '秒后重新获取'
              limitButton.disabled = 'disabled'
              if (num === 0) {
                limitButton.disabled = ''
                limitButton.innerHTML = '发送短信验证码'
                clearInterval(timer)
              }
            }, 1000)
          } else {
            alert('请重新获取短信验证码')
            return false
          }
        })
        .catch((error) => {
          return false
        })
    },

    // 设置安全问题——获取全部安全问题
    getAllSecurityQuestions: function () {
      let pram = {
        accessToken: localStorage.getItem('access_Token')
      }
      pram = this.$qs.stringify(pram)
      gapi.getUserSecurityQuestions(pram)
        .then((res) => {
          if (res.data.code === 1) {
            this.safetyOptions = res.data.data
          } else {
            return false
          }
        })
        .catch((error) => {
          return false
        })
    },

    // 发送用户设置的安全问题
    sendUsersSettingSafetyProblem: function () {
      let param = {
        question: this.safetyValue,
        answer: this.safetyValueAnswer
      }
      param = this.$qs.stringify(param)
      gapi.sendUsersSettingsSecurity(param)
      .then((res) => {
        if (res.code === 1 ) {
          this.deleteSettingSafetyProblem()
          this.ruleForm.username = ''
          this.ruleForm.password = ''
          this.ruleForm.verificationCode = ''
        }
      })
      .catch((error) => {
        return false
      })
    },

    // 获取用户安全问题
    requestUserSecurityIssues: function () {
      gapi.getUserQuestion()
        .then((res) => {
          if (res.code === 1) {
            this.answer = res.data
          } else {
            return false
          }
        })
        .catch((error) => {
          return false
        })
    },

    // 验证安全问题  跳转搜索页面
    jumpSearchPage: function () {
      let para = { answer: this.safetyProblemAnswer }
      gapi.sendUsersSecurityVerify(para)
        .then((res) => {
          if (res.data.code === 1) {
            localStorage.setItem('access_Token', res.data.data)
            let _this = this
            setTimeout(function () {
              _this.$router.push('/home')
            }, 1000)
          } else {
            return false
          }
        })
        .catch((error) => {
          return false
        })
    },

    // 验证——不能输入空格,中文及特殊符号
    noSpace (name) {
      this.ruleForm[name] = this.ruleForm[name].replace(/[^\w/]/ig, '')
      this.ruleForm[name] = this.ruleForm[name].replace(/[_, ]/g, '')
      // 当输入用户名时，清除密码，同时 实时判断 本地是否有 正在输入的账号，如果有 则 自动将密码赋值
      if (name == 'username') {
        this.ruleForm.password = ''
        for (let key in this.getLocalUser) {
          if (this.getLocalUser[key].username === this.ruleForm.username) {
            this.ruleForm.password = this.getLocalUser[key].password
          }
        }
      }
    }
  }
}
</script>
<style >
  .login-wrap {
    position: relative;
    width:100%;
    height:100%;
    background-color: #3F4262;
  }
  .login_plate_top, .login_plate_bottom {
    width: 100%;
    height: 50%;
  }
  .login_plate_top {
    background: #4778C7;
  }
  .login_plate_bottom {
    background: #E9E9E9;
  }
  .login-wrap .loginBoard {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
  }
  .ms-title{
    background: url("../../assets/images/lo_go.png") no-repeat 100%;
    width: 260px;
    height: 90px;
    display: block;
    margin: auto;
  }
  .demo-ruleForm .textInput {
    margin-bottom: 36px;
  }
  .login-wrap .demo-ruleForm .el-form-item {
    margin-bottom: 26px;
  }
  .demo-ruleForm .el-form-item__error {
    padding-top: 5px;
  }
  .ms-login{
    background: rgba(255,255,255,1);
    margin: 40px auto 0;
    padding: 50px;
    box-shadow: 0 0 10px 2px rgba(0,0,0,.1);
  }
  .ms-login .login_title {
    font-size: 14px;
    color: #2E2E30;
    font-family: MicrosoftYaHei;
    text-align: left;
    margin-bottom: 14px;
  }
  .verificationCode {
    text-align: left;
  }
  .verificationCode .el-input{
    text-align: left;
    width: 50%;
    vertical-align: top;

  }
  .verificationCode .el-input input {
    line-height: 40px;
    height: 40px;
  }
  .verificationCodeImage {
    display: inline-block;
    margin: 0 20px;
  }
  .verificationCodeImage img {
    display: block;
  }
  .refreshVerificationCode {
    cursor: pointer;
    color: rgba(46,46,48,1);
    font-size: 16px;
    vertical-align: top;
  }
  .textForgetPassword {
    font-size: 14px;
    text-align: right;
    cursor: pointer;
    color: rgba(46,46,48,1);
    font-family: MicrosoftYaHei;
    margin-top: -20px;
  }
  .textInput .el-input__inner{
    width: 420px;
    height: 50px;
    line-height: 50px;
    border-radius: 5px;
    border: 1px solid #E5E5E5!important;
    color: #1B1D2B;
    font-size: 18px;
  }
  .login-btn{
    margin-top: 30px;
    text-align: center;
  }
  .login-btn button{
    width: 100%;
    height: 45px;
    font-size: 16px;
    border-radius: 5px;
  }
  .shadow_mask {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 998;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.9);
  }
  .personal_information, .setting_safety_problem, .safety_problem {
    z-index: 999;
    position: fixed;
    top: 50%;
    left: 50%;
    transform:translate(-50%,-50%);
    text-align: center;
    background: #ffffff;
    border-radius: 5px;
    padding: 30px 70px 30px 50px;
  }
  .personal_information .close_icon, .safety_problem .close_icon, .setting_safety_problem .close_icon {
    display: block;
    position: absolute;
    top: -16px;
    right: -16px;
    background: url("../../assets/images/delete.png") no-repeat 100%;
    width: 32px;
    height: 32px;
  }
  .personal_information .title, .safety_problem .title, .setting_safety_problem .t_title {
    margin-bottom: 20px;
    text-align: center;
    font-size: 18px;
    color: #3F4262;
  }
  .set_confirm_button {
    margin-top: 28px;
  }
  .safety_problem p i {
    /* float: left; */
    display: inline-block;
  }
</style>



